import styled from "styled-components";

export const Container = styled.div`

align-items: center;

`

export const Title = styled.h1`
text-align: center;
      background-image:  linear-gradient(hsla(0,20%,50%,.5),hsla(0,60%,50%,.5)),
    url('https://imgur.com/d2kFK.gif'),
    linear-gradient(orange,maroon); 
  background-size: 100%;
  background-position: 50% 50%;
  color: white;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;  
  font-size: 7em;
  /* line-height: .8; */
  display: inline-block;
  height: 100%;
  width: 100%;
  top: 20%;
`


